<template>
  <div class="goods">
    <van-icon name="arrow-left" @click="onClickLeft"/>
    <!-- swiper -->
    <van-swipe @change="onChange">
      <van-swipe-item v-for="(e, i) in current.picurl" :key="i" @click="preview">
        <img :src="e" alt="">
      </van-swipe-item>
      <div class="custom-indicator" slot="indicator">
        {{ index + 1 }}/{{current.picurl.length}}
      </div>
    </van-swipe>   

     <!-- info -->
    <section class="header">
      <div class="price-group">
        <p class="price"><span>￥</span>{{current.price}}<span>起</span></p>
        <del>￥{{current.normalprice}}</del>
        <p class="count">已拼{{current.count}}件</p>
        <p class="desc">支持分期，低至114.66元/期</p>
      </div>
      <div class="title">
        <h2>{{current.name}}</h2>
        <span class="tag">送货入户并安装</span>
      </div>
      <div class="option">
        <div class="ticket">
          <span class="tag">领券</span>
          <span class="tag-o" v-for="(e, i) in current.tickettag" :key="i">{{e}}</span>
        </div>
        <div class="service">
          <span v-for="(e, i) in current.servicetag" :key="i">{{e}}</span>
        </div>
      </div>
    </section>

    <!-- bar -->
    <div class="goods-bottom-bar">
      <van-goods-action>
        <van-goods-action-icon to="/home" icon="wap-home" text="主页" />
        <van-goods-action-icon icon="chat-o" @click="onDevHandle" text="客服" />
        <van-goods-action-icon to="/cart" icon="cart-o" text="购物车" :info="cart.length || 0" />
        <van-goods-action-button type="warning" @click="collection(current)" text="加入购物车" />
        <van-goods-action-button type="danger" disabled text="立即购买" />
      </van-goods-action>
    </div>

    <!-- popup -->
    <van-popup class="order" :style="{height: '70%'}" v-model="show" position="bottom">
      <div class="title">
        <img src="http://temp.im/150x150" alt="">
        <div class="cont">
          <p class="price">2660</p>
          <p class="desc">已选：{{}}</p>
        </div>
      </div>
      <p class="btn">确定</p>
    </van-popup>
    <transition name="animate">
    <div v-if="animateShow" class="animate"></div>
    </transition>
  </div>
</template>
<script>
import { ImagePreview } from 'vant'
import { mapState, mapMutations, mapGetters } from 'vuex'
import { Toast } from 'vant'
export default {
  data() {
    return {
      index: 0,
      show: false,
      animateShow: false,
    }
  },
  computed: {
    ...mapState('goods', ['current', 'cart']),
  },
  methods: {
    ...mapMutations('goods', ['addGoods']),
    collection(goods) {
      this.animateShow = true
      setTimeout(() => {
        this.animateShow = false
        this.addGoods(goods)
      }, 500);
    },
    onChange(index) {
      this.index = index
    },
    onClickLeft() {
      this.$router.back()
    },
    buy() {
      this.show = true
    },
    onDevHandle() {
      Toast('开发中...')
    },
    preview() {
      ImagePreview({
        images: this.current.picurl,
        startPosition: this.index,
      })
    },
  }
}
</script>
<style lang="scss" scoped>
@import '@/assets/css/util.scss';

/deep/ .van-swipe {
  height: 7.5rem;
  background-color: #eee;
}
/deep/ .van-popup {
  overflow: visible;
}
.van-icon-arrow-left {
  z-index: 1;
  @include pf-lt(.2rem, .2rem);
  font-size: .48rem;
}
.animate {
  @include pf-lt(10%, 60%, .3rem, .3rem);
  border-radius: 50%;
  background-color: #e02e24;
}
.animate-leave {
  transform: translate(0, 0) scale(1);
}
.animate-leave-active {
  transition: 1s;
}
.animate-leave-to {
  transform: translate(1.5rem, 35vh) scale(.2);
}
.goods {
  background-color: #f4f4f4;
}
.custom-indicator {
  @include pa-rb(.2rem, .2rem, $w:auto);
  color: #aaa;
  font-size: .23rem;
}
.van-swipe-item {
  img {
    @include img(100%);
  }
}
.header {
  padding: .2rem .2rem 0;
  background-color: #fff;
  .option {
    div {
      height: .76rem;
      border-bottom: 1px solid #eee;
    }
    .ticket {
      display: flex;
      align-items: center;
      .tag {
        @include tag($s:.24rem, $b:#f73951, $p:0 .05rem);
      }
      .tag-o {
        @include tag-o($s:.24rem, $c:#f73951, $b:#fff, $p:0 .05rem);
        margin-left: .2rem;
      }
    }
    .service {
      display: flex;
      align-items: center;
      span {
        font-size: 12px;
        &:not(:last-child)::after {
          content: '·';
          padding: .2rem;
        }
      }
    }
  }
  .price-group {
    position: relative;
    .price {
      color: #e02e24;
      font-size: 20px;
      font-weight: 700;
      span {
        font-size: 14px;
      }
    }
    .count {
      font-size: 14px;
      float: right;
    }
    del {
      font-size: 12px;
      vertical-align: .05rem;
    }
    .desc {
      font-size: 14px;
      padding-bottom: .1rem;
    }
  }
  .title {
    padding-bottom: .2rem;
    border-bottom: 1px solid #eee;
    h2 {
      font-size: 16px;
      font-weight: 700;
    }
    span {
      font-size: 14px;
    }
  }
}
.goods-bottom-bar {
  @include pf-lb(0, 0, 1.08rem);
  display: flex;
  align-items: stretch;
  .bar-item {
    flex: 0 0 .9rem;
    text-align: center;
    p {
      font-size: 12px;
    }
    .van-icon {
      font-size: 18px;
      color: #9b9b9b;
    }
  }
  .bar-btn {
    flex: auto;
    padding-top: .1rem;
    p {
      font-size: 16px;
    }
  }
  .single {
    color: #fff;
    background-color: #f3aba7;
  }
  .group {
    color: #fff;
    background-color: #e02e24;
  }
}
.order {
  .title {
    @include pr-h(1.4rem);
    border-bottom: 1px solid #eee;
    img {
      position: absolute;
      margin-left: .2rem;
      width: 1.6rem;
      top: -30%;
    }
    .cont {
      margin-left: 2rem;
      .price {
        color: #e02e24;
      }
      .desc {
        font-size: 14px;
      }
    }
  }
  .btn {
    @include pa-lb(0, 0, .88rem);
    @include cb(#fff, #e02e24, .32rem);
    line-height: .88rem;
    display: block;
    text-align: center;
  }
}
</style>


